.contact {
  height: 100vh;
  h1{
    text-align: center;
    padding-top: 100px;
    margin-bottom: 50px;
  }
}

.lead-form{
  text-align: center;
  font-size:20px;
}
form{
  width:600px;
  margin:0 auto;
}
.item{
  overflow: hidden;
  margin-bottom: 20px;
}
.label{
  float: left;
  margin-right: 20px;
  width:135px;
  border-left: solid 3px #e0505d;
  padding-left: 10px;
}
.inputs{
  float: left;
  width:400px;
}
input[type="text"],input[type="email"]{
  border: solid 1px #aaa;
  border-radius:5px;
  padding:10px;
  font-size: 15px;
}
textarea{
  border: solid 1px #aaa;
  border-radius:5px;
  padding: 10px;
  height: 160px;
  font-size: 15px;
}
.btn-area{
  text-align: center;
}
input[type="submit"]{
  cursor: pointer;
  background: #e0505c;
  border: none;
  color: white;
  font-size:17px;
  font-weight:bold;
  padding: 10px 100px;
  margin: 0 auto;
}

/* SP MediaQuery */
@media only screen and (max-width:767px) {
