.header {
  // background: rgba(255, 255, 255, 0.3);
  position: fixed;
  right: 0;
  margin-top: 10px;
  z-index: 1;
  // width: 100%;

  &__menu{
    color: rgb(51, 51, 51);
  }

  &__list {
    list-style: none;
    display: flex;
  }

  &__list li {
    width: 140px;
    text-align: center;
    height: 50px;
    line-height: 50px;
    margin-right: 2px;
  }

  &__list li a {
    text-decoration: none;
    color: rgb(26, 26, 26);
    font-weight: bold;
    padding: 20px;
    text-shadow:0.5px 0.5px 0 #FFF, -0.5px -0.5px 0 #FFF,
              -0.5px 0.5px 0 #FFF, 0.5px -0.5px 0 #FFF,
              0px 0.5px 0 #FFF,  0-0.5px 0 #FFF,
              -0.5px 0 0 #FFF, 0.5px 0 0 #FFF;
  }

}

/* SP MediaQuery */
@media only screen and (max-width:767px) {
.header {
  background: rgba(255, 255, 255, 0.3);
  position: fixed;
  right: 0;
  margin-top: 10px;
  z-index: 1;
  width: 100%;

  &__menu{
    color: rgb(51, 51, 51);
  }

  &__list {
    display: flex;
    flex-direction: column;
    list-style: none;
    display: flex;
  }

  &__list li {
    width: 100px;
    text-align: center;
    height: 50px;
    line-height: 50px;
    margin-right: 2px;
  }

  &__list li a {
    text-decoration: none;
    color: rgb(26, 26, 26);
    font-weight: bold;
    padding: 20px;
    text-shadow:0.5px 0.5px 0 #FFF, -0.5px -0.5px 0 #FFF,
              -0.5px 0.5px 0 #FFF, 0.5px -0.5px 0 #FFF,
              0px 0.5px 0 #FFF,  0-0.5px 0 #FFF,
              -0.5px 0 0 #FFF, 0.5px 0 0 #FFF;
  }

}
}
/* SP MediaQuery end */
