.profile {
    height: 100vh;
    width: 100%;

    &__img {
    margin-top: 10%;
    // margin-bottom: 100px;
    // margin-left: 50px;
    position: relative;
    position: absolute;
  }

  &__detail {
    background: rgba(255, 255, 255, 0.9);
    width: 700px;
    position: absolute;
    top: 140%;
    left: 40%;
    padding: 70px 30px;
    text-align: center;


    span{
      font-size: 20px;
    }

    h1{
      font-size: 30px;
      margin-top: 30px;
      margin-bottom: 30px;
    }

    h2{
      font-size: 20px;
    }

    p{
      margin-top: 40px;
      margin-right: 15%;
      margin-left: 15%;
      text-align: left;
      font-family: 'Kosugi Maru', sans-serif;
    }
  }
}

.square {
  height: 50px;
  width: 50px;
  background: #e0505c;
  position: absolute;
  top: 80%;
  right: 4%;
}

/* SP MediaQuery */
@media only screen and (max-width:767px) {
.profile {
    // height: 100vh;
    width: 90%;
    margin-right: 5%;
    margin-left: 5%;

    &__img {
    width: 80%;
    height: auto;
    margin-top: 30%;
    // margin-bottom: 100px;
    // margin-left: 50px;
    position: relative;
    position: absolute;
  }

  &__detail {
    background: rgba(255, 255, 255, 0.9);
    width: 70%;
    position: absolute;
    top: 170%;
    left:10%;
    padding: 30px 20px;
    text-align: center;

  }
}

.square {
  height: 20px;
  width: 20px;
  background: #e0505c;
  position: absolute;
  top: 90%;
  right: 4%;
}
}
/* SP MediaQuery end */
